body {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  color: #5e5757;
}

.loader {
  width: 50px;
  height: 50px;
  border: 8px solid #f3f3f3;
  border-top-color: #83bde4;
  border-radius: 50%;
  margin: 20px auto;
  animation: spin 2s linear infinite;
  display: none;
}

.error {
  color: red;
  font-weight: bold;
  display: none;
}

.breed-select {
  width: 200px;
  margin: 20px auto;
  padding: 5px;
}

.cat-info {
  margin-top: 20px;
  display: none;
}

.cat__wrapper {
  text-align: center;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  display: grid;
}

.breed-name:hover {
  text-decoration: underline;
}

.breed-name {
  color: #5e5757;
}

img {
  max-width: 50%;
}

